import React, { Component } from 'react';
import { ReactComponent as JS } from '../components/icons/js.svg';
import { ReactComponent as TS } from '../components/icons/ts.svg';
import { ReactComponent as HTML } from '../components/icons/html.svg';
import { ReactComponent as CSS } from '../components/icons/css.svg';
import { ReactComponent as POSTGRES } from '../components/icons/postgres.svg';
import { ReactComponent as PYTHON } from '../components/icons/python.svg';
import { ReactComponent as JAVA } from '../components/icons/java.svg';
import { ReactComponent as C_LANG } from '../components/icons/c.svg';
import { ReactComponent as NODEJS } from '../components/icons/nodejs.svg';
import { ReactComponent as EXPRESS } from '../components/icons/express.svg';
import { ReactComponent as MONGODB } from '../components/icons/mongodb.svg';
import { ReactComponent as REACT } from '../components/icons/react.svg';
import { ReactComponent as ELECTRON } from '../components/icons/electron.svg';
import { ReactComponent as GITHUB } from '../components/icons/github.svg';
import { ReactComponent as EMAIL } from '../components/icons/email.svg';
import { ReactComponent as DISCORD } from '../components/icons/discord.svg';
import { ReactComponent as LINKEDIN } from '../components/icons/linkedin.svg';
import { ReactComponent as PHP } from '../components/icons/php.svg';
import { ReactComponent as NEXT } from '../components/icons/next.svg';
import { ReactComponent as TAILWIND } from '../components/icons/tailwindcss.svg';
import { ReactComponent as SOLIDITY } from '../components/icons/solidity.svg';
import { ReactComponent as ETHEREUM } from '../components/icons/ethereum.svg';
import { ReactComponent as GO } from '../components/icons/go.svg';
import { motion } from 'framer-motion';

export default class Home extends Component {
	render() {
		return (
			<motion.div initial="initial" animate="in" exit="out" variants={this.props.pageVariants} transition={this.props.pageTransition}>
				<div className="main-content">
					<div className="left-column" style={{ margin: 'auto' }}>
						{' '}
						{/* eslint-disable-next-line */}
						<h1 id="title-text">{this.props.helloText}</h1> {/* eslint-disable-next-line */}
						<p id="content-text">{this.props.contText}</p>
						<a href="https://linkedin.com/in/nadhirxz" target="_blank" id="github">
							<LINKEDIN className="svg-link" width={style.width} height={style.height} title="LinkedIn" />
						</a>
						<a href="https://github.com/nadhirxz" target="_blank" id="github">
							<GITHUB className="svg-link" width={style.width} height={style.height} title="Github" />
						</a>
						<a href="mailto:nadhirxz@gmail.com" target="_blank" id="email">
							<EMAIL className="svg-link" width={style.width} height={style.height} title="Email" />
						</a>
						<a href="https://discord.com/users/626920219759083528" target="_blank" id="discord">
							<DISCORD className="svg-link" width={style.width} height={style.height} title="Discord" />
						</a>
					</div>
					<div className="right-column">
						<h4 id="languages">{this.props.langText}</h4>
						<JS style={style} title="JavaScript" />
						<TS style={style} title="TypeScript" />
						<HTML style={style} title="HTML5" />
						<CSS style={style} title="CSS3" />
						<PYTHON style={style} title="Python" />
						<JAVA style={style} title="Java" />
						<C_LANG style={style} title="C" />
						<GO style={style} title="Go" />
						<SOLIDITY style={style} title="Solidity" />
						<h4 id="tools">{this.props.toolsText}</h4>
						<NODEJS style={style} title="Node.js" />
						<EXPRESS style={style} title="Express.js" />
						<REACT style={style} title="React" />
						<NEXT style={style} title="Next" />
						<TAILWIND style={style} title="TailwindCSS" />
						<MONGODB style={style} title="MongoDB" />
						<POSTGRES style={style} title="PostgreSQL" />
						<ELECTRON style={style} title="Electron" />
						<ETHEREUM style={style} title="Ethereum" />

						<br />
						<br />

						<a id="skills-link">
							<button className="btn" onClick={this.props.goSkills} style={{ width: '40%' }}>
								Skills
							</button>
						</a>
						<br />

						<a id="cv-link" href='cv-nadhir-ferdji.pdf' target='_blank'>
							<button className="btn" style={{ width: '40%' }}>
								Resume
							</button>
						</a>
					</div>
				</div>
			</motion.div>
		);
	}
}
const style = {
	width: '50px',
	height: '50px',
	transition: 'opacity 2s',
};
